import React, {Component} from "react"
import {connect} from "react-redux"
import VoipVariantComponent from "./VoipVariantComponent";
import VoipNumbersComponent from "./VoipNumbersComponent";
import {fetchingVoipNumbersWithMultiCallBlock, mountVoipModalComponent} from "../actions/voip";
import {getWwtCityId} from "../selectors/root";
import {getDesignationNumber, getNumbersFetched} from "../selectors/voip";

class OraFixVoipModalComponent extends Component {

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.props.mountVoipModalComponent();
    }

    getDerivedStateFromProps(nextProps, state) {
        if ((!!nextProps.cityId && this.props.cityId !== nextProps.cityId)
            || this.props.designationNumber !== nextProps.designationNumber){
            this.props.fetchVoipNumbers(nextProps.cityId);
        }
    }

    render() {
        return (
            <div>
                <VoipVariantComponent descriptions={this.props.descriptions}/>
                <VoipNumbersComponent descriptions={this.props.descriptions}/>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    cityId: getWwtCityId(state),
    designationNumber: getDesignationNumber(state),
    numbersFetched: getNumbersFetched(state)
});

const mapDispatchToProps = dispatch => ({
    fetchVoipNumbers: (cityId) => dispatch(fetchingVoipNumbersWithMultiCallBlock(cityId)),
    mountVoipModalComponent: () => dispatch(mountVoipModalComponent())
});

export default connect(mapStateToProps, mapDispatchToProps)(OraFixVoipModalComponent);